.toggle-wrap{display:inline-flex;align-items:center}.toggle-wrap>span{color:#000;font-size:18px;margin-left:15px}.toggle-wrap label.checkbox-slider{position:relative;display:inline-block;min-width:51px;height:28px;margin:0}.toggle-wrap label.checkbox-slider input{opacity:0;width:0;height:0}.toggle-wrap label.checkbox-slider .slider{position:absolute;cursor:pointer;top:0;left:0;right:0;bottom:0;border-radius:12px;background-color:gray;transition:all ease .5s;box-shadow:inset -1px -1px 1px rgba(255,255,255,.25),inset 0px 1px 1px rgba(0,0,0,.3)}.toggle-wrap label.checkbox-slider .active-not{cursor:default}.toggle-wrap label.checkbox-slider .slider:before{content:"нет";font-size:16px;display:flex;justify-content:center;align-items:center;position:absolute;height:28px;width:34px;top:-10%;padding-bottom:2px;font-weight:450;color:#fff;background-color:#9f9c87;transition:all ease .5s;border-radius:50%;box-shadow:0 2px 4px rgba(0,0,0,.25),inset -1px -1px 1px rgba(0,0,0,.15),inset 1px 1px 1px rgba(255,255,255,.15)}.toggle-wrap label.checkbox-slider input:checked+.slider:before{content:"да";-webkit-transform:translateX(50%);transform:translateX(50%);background-color:#443f31}.toggle-wrap label.checkbox-is-delete{opacity:.5;pointer-events:none;cursor:auto}
.app-dropdown{position:relative;display:inline-flex;flex-direction:column;margin:0;padding:0;font-size:14px}.app-dropdown:not(.disable) .input-base{background-color:var(--main-color-bg)}.app-dropdown .dropdown-values-block{background-color:var(--main-color-bg);box-shadow:0 5px 12px rgba(0,118,182,.1);border:1px solid rgba(0,0,0,.28);border-radius:0 0 8px 8px;position:absolute;bottom:0;left:0;right:0;-webkit-transform:translateY(100%);transform:translateY(100%);z-index:10;max-height:220px;overflow-y:scroll;transition:.55s opacity,.55s visibility}.app-dropdown .dropdown-values-block .value-item{display:inline-flex;align-items:center;grid-column-gap:5px;-webkit-column-gap:5px;column-gap:5px;max-width:100%;width:100%;padding:5px;border-radius:4px;background-color:var(--main-color-bg);word-break:break-all;line-height:1.29;font-size:16px;cursor:pointer;color:#000}.app-dropdown .dropdown-values-block .value-item:hover{background-color:#9f9c87}.app-dropdown .dropdown-values-block .reset-filter{display:flex;justify-content:space-between;align-items:center;background:#fff;font-size:12px}.app-dropdown .dropdown-values-block::-webkit-scrollbar-thumb{background-color:#a9a9a9}.app-dropdown .dropdown-values-block::-webkit-scrollbar{width:2px}.pagination-mode div{margin-top:0}.pagination-mode input{max-width:50px;width:50px;border-radius:0 0 8px 8px}.pagination-mode .dropdown-values-block{border-radius:8px 8px 0 0;bottom:auto;top:0;-webkit-transform:translateY(-100%);transform:translateY(-100%)}
