.toggle-wrap{display:inline-flex;align-items:center}.toggle-wrap>span{color:#000;font-size:18px;margin-left:15px}.toggle-wrap label.checkbox-slider{position:relative;display:inline-block;min-width:51px;height:28px;margin:0}.toggle-wrap label.checkbox-slider input{opacity:0;width:0;height:0}.toggle-wrap label.checkbox-slider .slider{position:absolute;cursor:pointer;top:0;left:0;right:0;bottom:0;border-radius:12px;background-color:gray;transition:all ease .5s;box-shadow:inset -1px -1px 1px rgba(255,255,255,.25),inset 0px 1px 1px rgba(0,0,0,.3)}.toggle-wrap label.checkbox-slider .active-not{cursor:default}.toggle-wrap label.checkbox-slider .slider:before{content:"нет";font-size:16px;display:flex;justify-content:center;align-items:center;position:absolute;height:28px;width:34px;top:-10%;padding-bottom:2px;font-weight:450;color:#fff;background-color:#9f9c87;transition:all ease .5s;border-radius:50%;box-shadow:0 2px 4px rgba(0,0,0,.25),inset -1px -1px 1px rgba(0,0,0,.15),inset 1px 1px 1px rgba(255,255,255,.15)}.toggle-wrap label.checkbox-slider input:checked+.slider:before{content:"да";-webkit-transform:translateX(50%);transform:translateX(50%);background-color:#443f31}.toggle-wrap label.checkbox-is-delete{opacity:.5;pointer-events:none;cursor:auto}